body {
  margin: 0;
  padding: 0;
  background-color: antiquewhite;
}


.App-bg {
  position: absolute;
  left: 0 ;
  top: 0;
  border-radius: 20px;
  box-shadow: 10px 10px 20px 0px rgba(0,0,0,0.42);
}


.Button {
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
    width: 45px;
    margin: 5px;
    box-shadow: 0px 1px 0px 0px #fff6af;
    background:linear-gradient(to bottom, #ffec64 5%, #ffab23 100%);
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffec64', endColorstr='#ffab23',GradientType=0);
    background-color:#ffec64;
    border-radius:6px;
    border:1px solid #ffaa22;
    display:inline-block;
    cursor:pointer;
    color:#333333;
    text-shadow:0px 1px 0px #ffee66;
}
.Button:hover {
    background:linear-gradient(to bottom, #ffab23 5%, #ffec64 100%);
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffab23', endColorstr='#ffec64',GradientType=0);
    background-color:#ffab23;
}
.Button:active {
    position:relative;
    top:1px;
    outline: none;
}

.Button:focus {
    outline: none;
}

.Button:disabled {
    background: lightgrey;
}

.ButtonList {
    position: absolute;
    left: 15px;
    top: 330px;
    width: 400px;
}
@keyframes LetterPlate_appear-animation {
    from {opacity:0; top:250px; left:50px}
    to {opacity: 1; top:20px; left:20px}
}

.LetterPlate {
    background: white;
    width: 300px;
    height: 250px;
    position: absolute;
    left: 20px;
    top: 20px;
    text-align: center;
    box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
    animation-name: LetterPlate_appear-animation;
    animation-duration: 0.3s;
}

.LetterPlate-letter {
    font-size: 200px;
    font-family: 'Roboto', sans-serif;
    margin-top: 0px;
}
.word {
    background: white;
    width: 300px;
    height: 440px;
    position: absolute;
    left: 450px;
    top: 20px;
    box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
}

.word_invisible {
    opacity: 0;
}

@keyframes word_appear-animation {
    from {opacity:0; top:-100px; left:0px; transform: scale(0.1);}
    to {opacity: 1; top:20px; left:450px; transform: scale(1);}

}

.word_appear {
    animation-name: word_appear-animation;
    animation-duration: 0.5s;
    animation-timing-function: ease-out;
}

@keyframes word_disappear-animation {
    from {opacity:1; top:20px; }
    to {opacity: 0; top:200px; }

}

.word_disappear {
    animation-name: word_disappear-animation;
    animation-duration: 0.5s;
    animation-timing-function: ease-in;
    opacity: 0
}

.word-image {
    width:280px;
    height: 370px;
    object-fit: contain;
    margin-left: 10px;
    margin-top: 10px;
}

.word-caption {
    font-family: 'Roboto', sans-serif;
    font-size: 40px;
    margin: 0 0 0 10px;
}
